@charset "UTF-8";
@font-face {
  font-family: Playfair;
  font-display: swap;
  src: url("../fonts/Playfair-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: Playfair;
  font-display: swap;
  src: url("../fonts/Playfair-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: TildaSans;
  font-display: swap;
  src: url("../fonts/TildaSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: TildaSans;
  font-display: swap;
  src: url("../fonts/TildaSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
/* ------------------------------------------------------------------------------------------------------------------------------
ROOT
--------------------------------------------------------------------------------------------------------------------------------*/
:root {
  --site-width: 1440px;
  --container-width: 1240px;
  --desktop-padding: 100px;
  --lg-padding: 50px;
  --sm-padding: 15px;
  --font-main: "TildaSans", sans-serif;
  --font-decor: "Playfair", sans-serif;
  --black: #000;
  --white: #fff;
  --gold: #c69d07;
  --scrollbar: gray;
  --scrollbar-track: gray;
  --scrollbar-thumb: rgb(18, 18, 18);
  --gradient: linear-gradient(303deg, #f4de91 0%, #e4c559 42.31%, #e1c150 49%, #f0d883 68.27%, #f5d668 100%);
}

/* ------------------------------------------------------------------------------------------------------------------------------
RESET
--------------------------------------------------------------------------------------------------------------------------------*/
* {
  margin: 0;
  border: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus, *:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  inline-size: 100%;
  block-size: 100%;
}

body {
  overscroll-behavior: none;
  text-rendering: optimizelegibility;
  text-size-adjust: 100%;
  font-family: var(--font-main);
  font-size: 1rem;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

input[type=number] {
  appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

button {
  cursor: pointer;
  user-select: none;
  background-color: transparent;
}
button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

a {
  user-select: none;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:focus, a:active {
  outline: none;
}
a:visited {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

sup {
  color: inherit;
}

/* ------------------------------------------------------------------------------------------------------------------------------
TYPOGRAPHY
--------------------------------------------------------------------------------------------------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-main);
}

/* ------------------------------------------------------------------------------------------------------------------------------
BASE
--------------------------------------------------------------------------------------------------------------------------------*/
html {
  scroll-behavior: smooth;
}

body {
  text-wrap: balance;
  background-color: rgb(0, 0, 0);
}

*::-webkit-scrollbar {
  inline-size: 0.5rem;
  background-color: var(--scrollbar);
}
*::-webkit-scrollbar-track {
  border-radius: 0.625rem;
  box-shadow: inset 0 0 0.375rem rgba(0, 0, 0, 0.2);
  background: var(--scrollbar-track);
}
*::-webkit-scrollbar-thumb {
  border-radius: 0.625rem;
  border: 0 none #fff;
  background-color: var(--scrollbar-thumb);
  cursor: pointer;
}
@media (any-hover: hover) {
  *::-webkit-scrollbar-thumb:hover {
    background-color: grey;
  }
}

.container {
  margin-inline: auto;
  max-inline-size: var(--site-width);
  padding-inline: var(--desktop-padding);
}
@media (max-width: 992px) {
  .container {
    padding-inline: var(--lg-padding);
  }
}
@media (max-width: 576px) {
  .container {
    padding-inline: var(--sm-padding);
  }
}

.container-site {
  margin-inline: auto;
  max-inline-size: var(--site-width);
}

.container-full {
  padding-inline: var(--desktop-padding);
  max-width: 100%;
}
@media (max-width: 992px) {
  .container-full {
    padding-inline: var(--lg-padding);
  }
}
@media (max-width: 576px) {
  .container-full {
    padding-inline: var(--sm-padding);
  }
}

.img-responsive {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
}

.screen-reader-text {
  position: absolute;
  left: -9999px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  position: static;
  width: auto;
  height: auto;
  clip: auto;
  background: #000; /* или другой фон для выделения */
  color: #fff; /* цвет текста */
  padding: 8px;
  z-index: 1000;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0 0 0 0) !important;
  white-space: nowrap !important; /* предотвращает перенос текста */
  border: 0 !important;
}

.no-scroll {
  overflow: hidden;
}

.no-event {
  pointer-events: none;
}

.image-bg {
  position: relative;
}
.image-bg img {
  display: block;
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  object-fit: cover;
  block-size: 100%;
  inline-size: 100%;
}

.wrapper {
  display: flex;
  min-block-size: 100%;
  /** Поменять на clip - если используется в проекте position: sticky */
  overflow: hidden;
  flex-direction: column;
}

.main {
  position: relative;
  flex: 1 1 auto;
}

/* -----------------------------------------------------------------------------------------------------------------------------
BOOTSRTAP EXTRA
--------------------------------------------------------------------------------------------------------------------------------*/
.g-custom-40,
.gx-custom-40 {
  --bs-gutter-x: 40px;
}
@media (max-width: 576px) {
  .g-custom-40,
  .gx-custom-40 {
    --bs-gutter-x: 10px;
  }
}

.g-custom-40,
.gy-custom-40 {
  --bs-gutter-y: 40px;
}
@media (max-width: 576px) {
  .g-custom-40,
  .gy-custom-40 {
    --bs-gutter-y: 40px;
  }
}

/* ------------------------------------------------------------------------------------------------------------------------------
COMPONENTS
--------------------------------------------------------------------------------------------------------------------------------*/
.section__title {
  position: relative;
  z-index: 1;
  user-select: none;
  font-weight: 400;
  font-size: 2.9375rem;
  line-height: 110%;
  text-align: center;
}
.section__title .title-decor {
  font-family: var(--font-decor);
  font-style: italic;
  font-weight: 700;
  font-size: 3.125rem;
}
@media (max-width: 576px) {
  .section__title .title-decor {
    font-size: 1.75rem;
  }
}
@media (max-width: 576px) {
  .section__title {
    font-size: 1.75rem;
  }
}

[class*=_block] {
  padding-block: 6.25rem;
  border-radius: 6.25rem;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
@media (max-width: 768px) {
  [class*=_block] {
    border-radius: 3.125rem;
    padding-block: 4.375rem;
  }
}

[class*=_part] {
  position: relative;
  padding-block: 7.5rem;
}
[class*=_part]:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-repeat: repeat-y;
  background-size: 100%;
  background-position: left top;
}
@media (max-width: 768px) {
  [class*=_part] {
    padding-block: 4.375rem;
  }
}

[class*=_lang] {
  display: inline-flex;
  align-items: center;
  gap: 0.625rem;
}
[class*=_lang] .btn-lang {
  font-weight: 400;
  font-size: 0.75rem;
  color: var(--white);
  opacity: 0.4;
  transition: opacity 0.3s ease-in-out;
}
[class*=_lang] .btn-lang:hover {
  opacity: 0.55;
}
[class*=_lang] .btn-lang.active {
  opacity: 0.8;
}

.btn-accent,
.btn-callback {
  font-weight: 700;
  font-size: 0.75rem;
  color: #4e421b;
  border-radius: 1.25rem;
  padding-inline: 1.25rem;
  min-height: 2.125rem;
  display: flex;
  align-items: center;
  background: linear-gradient(303deg, #f4de91 0%, #e4c559 42.31%, #e1c150 49%, #f0d883 68.27%, #f5d668 100%);
}

.burger {
  position: relative;
  display: inline-block;
  width: 1.25rem;
  height: 0.8125rem;
  flex-shrink: 0;
  cursor: pointer;
}
.burger span, .burger::before, .burger::after {
  display: block;
  content: "";
  transition: all 0.3s ease-in-out;
  position: absolute;
  left: 0;
  width: 100%;
  height: 1.5px;
  background-color: var(--black);
  border-radius: 0.625rem;
}
.burger::before {
  top: 0;
}
.burger::after {
  bottom: 0;
}
.burger span {
  top: 50%;
  transform: translateY(-65%);
}
.burger.active span {
  width: 0;
  background-color: var(--white);
}
.burger.active::before {
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
  background-color: var(--white);
}
.burger.active::after {
  bottom: 50%;
  transform: translateY(50%) rotate(45deg);
  background-color: var(--white);
}

.request-form {
  color: var(--black);
  border-radius: 1.25rem;
  padding: 1.875rem;
  background-color: var(--white);
  max-width: 24.1875rem;
  min-width: 18.125rem;
  width: 100%;
}
.request-form__title {
  font-size: 1.75rem;
  text-align: center;
  margin-bottom: 0.9375rem;
}
.request-form__description {
  text-align: center;
  font-size: 0.875rem;
  margin-bottom: 2.1875rem;
}
.request-form__group {
  display: flex;
  align-items: start;
  gap: 0.5rem;
}
.request-form__group input:not([type=checkbox], [type=radio]) {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.625rem;
  width: 100%;
  min-height: 3.125rem;
  padding-inline: 0.9375rem;
  font-size: 0.875rem;
  color: var(--black);
}
.request-form__group input:not([type=checkbox], [type=radio])::placeholder {
  color: var(--black);
}
.request-form__group .custom-checkbox {
  position: relative;
  top: -4px;
}
.request-form__group:nth-of-type(1) {
  margin-bottom: 0.9375rem;
}
.request-form__group:nth-of-type(2) {
  margin-bottom: 1.1875rem;
}
.request-form .btn-accent {
  margin-top: 1.0625rem;
}
.request-form .privacy {
  font-size: 0.875rem;
}
.request-form .privacy a {
  color: var(--balck);
  text-decoration: underline;
}
.request-form .privacy a:hover {
  text-decoration: none;
}

.custom-checkbox {
  flex-shrink: 0;
  appearance: none;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  width: 24px;
  height: 24px;
  background-color: var(--white);
  cursor: pointer;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.custom-checkbox:checked {
  background-image: url("data:image/svg+xml;base64,");
  background-repeat: no-repeat;
  background-size: 70%;
  background-position: center center;
}

.custom-checkbox:hover {
  outline: 1px solid var(--accentlight);
}

.custom-checkbox:active {
  outline: 1px solid var(--accentlightest);
}

.cases-card {
  display: flex;
  flex-direction: column;
  gap: 0.9375rem;
}
.cases-card__image {
  aspect-ratio: 1/1;
  overflow: hidden;
  width: 100%;
  border-radius: 1.25rem;
  border: 1px solid transparent;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
}
.cases-card__image:hover {
  border: 1px solid var(--gold);
}
.cases-card__image:hover img {
  transform: scale(1.1);
}
.cases-card__image img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}
.cases-card__tags {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 0.3125rem;
}
.cases-card__tag {
  border-radius: 2.25rem;
  padding: 4px 8px 6px;
  background-color: var(--black);
  font-weight: 700;
  font-size: 0.625rem;
  text-align: center;
  color: var(--white);
  transition: all 0.3s ease-in-out;
}
.cases-card__tag:hover {
  background-color: var(--gold);
  color: var(--black);
}
.cases-card__link {
  color: var(--black);
}
.cases-card__link:hover .cases-card__title {
  color: var(--gold);
}
.cases-card__title {
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 120%;
  transition: all 0.3s ease-in-out;
  margin-bottom: 0.75rem;
}
.cases-card__description {
  font-size: 0.75rem;
  line-height: 120%;
}

/* ------------------------------------------------------------------------------------------------------------------------------
FOOTER
--------------------------------------------------------------------------------------------------------------------------------*/
.footer {
  background-color: var(--black);
}
.footer .container {
  min-height: 13.875rem;
}
@media (max-width: 992px) {
  .footer .container {
    min-height: 9.6875rem;
  }
}
.footer__row {
  display: flex;
  justify-content: space-between;
  padding-block: 3.125rem;
}
.footer__logo {
  font-weight: 700;
  font-size: 2.875rem;
  color: var(--white);
}
@media (max-width: 992px) {
  .footer__logo {
    font-size: 2.25rem;
  }
}
@media (max-width: 768px) {
  .footer__logo {
    font-size: 1.75rem;
  }
}
.footer__group {
  display: inline-flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media (max-width: 576px) {
  .footer__group {
    gap: 0.9375rem;
  }
}
.footer__politic {
  font-weight: normal;
  font-size: 0.75rem;
  color: var(--white);
  opacity: 0.4;
  transition: opacity 0.3s ease-in-out;
}
.footer__politic:hover {
  opacity: 0.8;
}
.footer__nav ul {
  display: inline-flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media (max-width: 576px) {
  .footer__nav ul {
    gap: 0.625rem;
  }
}
.footer__nav li a {
  font-weight: 400;
  font-size: 0.75rem;
  color: var(--white);
  opacity: 0.4;
  transition: opacity 0.3s ease-in-out;
}
.footer__nav li a:hover {
  opacity: 0.8;
}

/* ------------------------------------------------------------------------------------------------------------------------------
HEADER
--------------------------------------------------------------------------------------------------------------------------------*/
.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
}
.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 4.375rem;
}
.header__logo {
  font-weight: 700;
  font-size: 1rem;
  color: var(--white);
}
@media (max-width: 768px) {
  .header__nav {
    position: fixed;
    top: 0;
    max-height: 100vh;
    height: 100%;
    right: -100%;
    width: 75%;
    transition: all 0.3s ease-in-out;
    overflow-y: auto;
    z-index: 5;
    background-color: #363636;
    padding-bottom: 2.5rem;
    background-image: url("../images/page/webp/page-1-1-thumb.webp");
    border-left: 1px solid #363636;
  }
  .header__nav.active {
    right: 0;
  }
}
.header__nav ul.menu {
  display: flex;
  align-items: center;
  gap: 1.5625rem;
}
@media (max-width: 768px) {
  .header__nav ul.menu {
    flex-direction: column;
    gap: 0.9375rem;
    padding-top: 20%;
    padding-bottom: 1.25rem;
  }
}
.header__nav ul.menu > li {
  position: relative;
  font-weight: 400;
  font-size: 0.75rem;
  color: var(--white);
}
.header__nav ul.menu > li:hover > a {
  text-decoration: underline;
}
.header__nav ul.menu > li:hover > a.no-event {
  text-decoration: none;
}
@media (max-width: 768px) {
  .header__nav ul.menu > li {
    width: 100%;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .header__nav ul.menu > li:hover > ul.submenu {
    opacity: 1;
    visibility: visible;
  }
}
.header__nav ul.menu > li span, .header__nav ul.menu > li a {
  font-weight: 400;
  font-size: 0.75rem;
  color: var(--white);
  cursor: pointer;
}
@media (max-width: 768px) {
  .header__nav ul.menu > li span, .header__nav ul.menu > li a {
    font-size: 1.125rem;
  }
}
.header__nav ul.submenu {
  opacity: 0;
  visibility: hidden;
  display: flex;
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(15px);
  flex-direction: column;
  gap: 0.4375rem;
  border-radius: 0.625rem;
  padding: 0.875rem 1.25rem;
  min-width: 13rem;
  background: var(--white);
  transition: all 0.3s ease-in-out 0.3s;
}
@media (max-width: 768px) {
  .header__nav ul.submenu {
    position: relative;
    opacity: 1;
    visibility: visible;
    margin-bottom: 0.9375rem;
    margin-inline: 0.9375rem;
    gap: 0.9375rem;
  }
  .header__nav ul.submenu li {
    line-height: 120%;
  }
}
.header__nav ul.submenu li,
.header__nav ul.submenu li a {
  font-weight: 400;
  font-size: 0.75rem;
  color: var(--black);
}
.header__nav ul.submenu li:hover a,
.header__nav ul.submenu li a:hover a {
  text-decoration: underline;
}
.header__nav .btn-lang {
  font-size: 1.125rem;
}
.header__nav .btn-callback {
  font-size: 1rem;
}
.header__group {
  display: flex;
  align-items: center;
  gap: 1.875rem;
}
@media (max-width: 768px) {
  .header__group {
    flex-direction: column;
    justify-content: center;
    gap: 1.25rem;
  }
}
.header__close {
  display: none;
  position: absolute;
  top: 1.875rem;
  left: 1.875rem;
  align-items: center;
  justify-content: center;
}
@media (max-width: 768px) {
  .header__close {
    display: block;
  }
}
.header__button {
  display: none;
  align-items: center;
  justify-content: center;
  min-height: 2.125rem;
  width: 2.125rem;
  padding-inline: 0;
  background: linear-gradient(303deg, #f4de91 0%, #e4c559 42.31%, #e1c150 49%, #f0d883 68.27%, #f5d668 100%);
}
@media (max-width: 768px) {
  .header__button {
    display: flex;
  }
}

/* --------------------------------------------------------------------------------------------------------------------------
MAIN CASES --- BLOCK 8
--------------------------------------------------------------------------------------------------------------------------------*/
.main-cases {
  color: var(--black);
}
.main-cases .section__title {
  margin-bottom: 4.0625rem;
}
@media (max-width: 576px) {
  .main-cases .section__title {
    margin-bottom: 2.5rem;
  }
}
.main-cases__part:before {
  background-image: url("../images/main/webp/main-8-bg.webp");
}
.main-cases .btn-accent {
  margin-top: 3.75rem;
  margin-inline: auto;
}
@media (max-width: 576px) {
  .main-cases .btn-accent {
    margin-top: 2.5rem;
  }
}

/* --------------------------------------------------------------------------------------------------------------------------
MAIN FIRST - 1
--------------------------------------------------------------------------------------------------------------------------------*/
.main-first__block {
  color: var(--white);
  position: relative;
  min-height: 44.5rem;
  background-image: url("../images/main/webp/main-1-bg.webp");
  background-position: 65% bottom;
  border-radius: 0 0 6.25rem 6.25rem;
}
@media (max-width: 768px) {
  .main-first__block {
    border-radius: 0 0 3.125rem 3.125rem;
  }
}
.main-first__block:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  background-image: url("../images/blind-left.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  aspect-ratio: 42/143;
}
.main-first__block:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  background-image: url("../images/blind-right.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  aspect-ratio: 42/143;
}
.main-first__description {
  z-index: 1;
  font-weight: 700;
  font-size: 1rem;
  line-height: 140%;
  max-width: 16.875rem;
  position: absolute;
  top: 45%;
  left: 15%;
}
@media (max-width: 576px) {
  .main-first__description {
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.7);
  }
}

/* --------------------------------------------------------------------------------------------------------------------------
MAIN WITH WE --- 5
--------------------------------------------------------------------------------------------------------------------------------*/
.main-withyou {
  color: var(--white);
}
.main-withyou__block {
  min-height: 37.25rem;
  background-image: url("../images/main/webp/main-5-bg.webp");
}
.main-withyou__row {
  counter-reset: myCounter; /* сбросить счетчик */
