/* ------------------------------------------------------------------------------------------------------------------------------
COMPONENTS
--------------------------------------------------------------------------------------------------------------------------------*/
.section__title {
	position: relative;
	z-index: 1;
	user-select: none;

	font-weight: 400;
	font-size: rem(47);
	line-height: 110%;

	text-align: center;

	.title-decor {
		font-family: var(--font-decor);
		font-style: italic;
		font-weight: 700;
		font-size: rem(50);
		@include sm {
			font-size: rem(28);
		}
	}

	@include sm {
		font-size: rem(28);
	}
}

[class*="_block"] {
	padding-block: rem(100);
	border-radius: rem(100);

	overflow: hidden;

	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;

	@include md {
		border-radius: rem(50);
		padding-block: rem(70);
	}
}

[class*="_part"] {	
	position: relative;

	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
	
		background-repeat: repeat-y;
		background-size: 100%;
		background-position: left top;
	}

	padding-block: rem(120);
	@include md {
		padding-block: rem(70);
	}
}

[class*="_lang"] {
	display: inline-flex;
	align-items: center;
	gap: rem(10);

	.btn-lang {
		font-weight: 400;
		font-size: rem(12);
		color: var(--white);

		opacity: 0.4;
		transition: opacity 0.3s ease-in-out;

		&:hover {
			opacity: 0.55;
		}
		&.active {
			opacity: 0.8;
		}
	}
}

.btn-accent,
.btn-callback {
	font-weight: 700;
	font-size: rem(12);
	color: #4e421b;

	border-radius: rem(20);
	padding-inline: rem(20);
	min-height: rem(34);

	display: flex;
	align-items: center;

	background: linear-gradient(303deg, #f4de91 0%, #e4c559 42.31%, #e1c150 49%, #f0d883 68.27%, #f5d668 100%);
}

// BURGER BUTTON
.burger {
	position: relative;
	display: inline-block;

	width: rem(20);
	height: rem(13);

	flex-shrink: 0;

	cursor: pointer;

	span,
	&::before,
	&::after {
		display: block;
		content: "";
		transition: all 0.3s ease-in-out;
		position: absolute;
		left: 0;
		width: 100%;
		height: 1.5px;
		background-color: var(--black);
		border-radius: rem(10);
	}
	&::before {
		top: 0;
	}
	&::after {
		bottom: 0;
	}
	span {
		top: 50%;
		transform: translateY(-65%);
	}
	&.active span {
		width: 0;
		background-color: var(--white);
	}
	&.active::before {
		top: 50%;
		transform: translateY(-50%) rotate(-45deg);
		background-color: var(--white);
	}
	&.active::after {
		bottom: 50%;
		transform: translateY(50%) rotate(45deg);
		background-color: var(--white);
	}
}

// REQUEST FFORM
.request-form {
	color: var(--black);

	border-radius: rem(20);
	padding: rem(30);
	background-color: var(--white);

	max-width: rem(387);
	min-width: rem(290);
	width: 100%;

	&__title {
		font-size: rem(28);
		text-align: center;

		margin-bottom: rem(15);
	}

	&__description {
		text-align: center;
		font-size: rem(14);

		margin-bottom: rem(35);
	}

	&__group {
		display: flex;
		align-items: start;
		gap: rem(8);

		input:not([type="checkbox"], [type="radio"]) {
			border: 1px solid rgba(0, 0, 0, 0.2);
			border-radius: rem(10);
			width: 100%;
			min-height: rem(50);
			padding-inline: rem(15);

			font-size: rem(14);
			color: var(--black);

			&::placeholder {
				color: var(--black);
			}
		}
		.custom-checkbox {
			position: relative;
			top: -4px;
		}
	}
	&__group:nth-of-type(1) {
		margin-bottom: rem(15);
	}
	&__group:nth-of-type(2) {
		margin-bottom: rem(19);
	}

	.btn-accent {
		margin-top: rem(17);
	}
	.privacy {
		font-size: rem(14);
		a {
			color: var(--balck);
			text-decoration: underline;
			&:hover {
				text-decoration: none;
			}
		}
	}
}

// CUSTOM CHECKBOX
.custom-checkbox {
	flex-shrink: 0;
	appearance: none;

	border: 1px solid rgba(0, 0, 0, 0.2);
	border-radius: 6px;
	width: 24px;
	height: 24px;
	background-color: var(--white);

	cursor: pointer;
	transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.custom-checkbox:checked {
	background-image: url("data:image/svg+xml;base64,");
	background-repeat: no-repeat;
	background-size: 70%;
	background-position: center center;
}
.custom-checkbox:hover {
	outline: 1px solid var(--accentlight);
}
.custom-checkbox:active {
	outline: 1px solid var(--accentlightest);
}

// CASES CARD
.cases-card {
	display: flex;
	flex-direction: column;
	gap: rem(15);

	&__image {
		aspect-ratio: 1/1;
		overflow: hidden;
		width: 100%;
		border-radius: rem(20);

		border: 1px solid transparent;
		transition: all 0.3s ease-in-out;

		display: flex;
		align-items: center;

		&:hover {
			border: 1px solid var(--gold);
			img {
				transform: scale(1.1);
			}
		}

		img {
			display: block;
			width: 100%;
			height: auto;
			object-fit: cover;
			overflow: hidden;

			transition: all 0.3s ease-in-out;
		}
	}
	&__tags {
		display: inline-flex;
		flex-wrap: wrap;
		align-items: center;
		gap: rem(5);
	}

	&__tag {
		border-radius: rem(36);
		padding: 4px 8px 6px;

		background-color: var(--black);

		font-weight: 700;
		font-size: rem(10);
		text-align: center;
		color: var(--white);

		transition: all 0.3s ease-in-out;

		&:hover {
			background-color: var(--gold);
			color: var(--black);
		}
	}

	&__link {
		color: var(--black);
		&:hover {
			.cases-card__title {
				color: var(--gold);
			}
		}
	}

	&__title {
		font-weight: 700;
		font-size: rem(20);
		line-height: 120%;

		transition: all 0.3s ease-in-out;

		margin-bottom: rem(12);
	}

	&__description {
		font-size: rem(12);
		line-height: 120%;
	}
}
